@charset "UTF-8";
.posts a {
  color: #444;
  text-decoration: none; }

.posts a:hover {
  color: #777; }

.posts .posts-list {
  list-style-type: none;
  float: left;
  width: 33.3%; }
  .posts .posts-list .post-link {
    text-align: center;
    line-height: 1.75em;
    padding: 10px;
    /* [上][右][下][左] */
    margin: 0px 5px 10px 5px;
    box-shadow: 1px 1px 1px 1px #ccc; }

.relate a {
  color: #444;
  text-decoration: none; }

.relate a:hover {
  color: #777; }

.relate h1 {
  font-weight: bold; }

.relate ul {
  list-style-type: none;
  width: 100%; }
  .relate ul li {
    width: 165px;
    float: left;
    text-align: center;
    line-height: 1.75em;
    padding: 10px;
    /* [上][右][下][左] */
    margin: 0px 5px 10px 5px;
    box-shadow: 1px 1px 1px 1px #ccc; }

.relate ul:after {
  content: "";
  display: block;
  clear: both; }

.tags .tags-title {
  font-weight: bold;
  border-bottom: 1px solid #e7e7e7;
  padding: 0px 0px 10px 0px;
  /* [上][右][下][左] */
  margin: 20px 0px 20px 0px; }

.tags a {
  color: #444;
  text-decoration: none; }

.tags a:hover {
  color: #777; }

.tag__0 {
  border-color: #a6cee3; }

.tag__1 {
  border-color: #1f78b4; }

.tag__2 {
  border-color: #b2df8a; }

.tag__3 {
  border-color: #33a02c; }

.tag__4 {
  border-color: #fb9a99; }

.tag__5 {
  border-color: #e31a1c; }

.tag__6 {
  border-color: #fdbf6f; }

.tag__7 {
  border-color: #ff7f00; }

.tag__8 {
  border-color: #cab2d6; }

.tag__9 {
  border-color: #6a3d9a; }

.tag__10 {
  border-color: #ffff99; }

.tag__11 {
  border-color: #b15928; }

.tag__12 {
  border: solid #aaa 1px;
  background-color: #f7f7f7; }

#title {
  color: #4a4a4a;
  margin-bottom: 0.35em;
  font-size: 3em;
  line-height: 40px;
  text-transform: uppercase; }

#blog-subtitle {
  font-size: 16px;
  padding-top: 0.25em;
  margin-bottom: 2em;
  font-weight: 600;
  color: #787878;
  max-width: 820px;
  /* [上][右][下][左] */
  padding: 0px 45px 0px 0px; }

#blog-content {
  float: left;
  max-width: 810px; }

#blog-menu {
  float: right;
  width: 180px; }
  #blog-menu ul {
    list-style-type: none; }
    #blog-menu ul li.recent {
      line-height: 1.5;
      font-size: 12px;
      /* [上][右][下][左] */
      padding: 3px 5px 3px 5px;
      margin: 0px 0px 3px 0px;
      box-shadow: 1px 1px 1px 1px #ccc; }
    #blog-menu ul li.tag-color-label {
      font-size: 12px;
      /* [上][右][下][左] */
      padding: 0px 3px 0px 3px;
      margin: 3px 0px 7px 0px;
      border-left-width: 5px;
      border-left-style: solid;
      box-shadow: 1px 1px 1px 1px #ccc; }

#blog-date {
  float: left; }

#blog-tags {
  float: right; }

#blog-menu li a, #blog-tags li a {
  color: #4078c0;
  text-decoration: none; }

#blog-menu li a:active,
#blog-menu li a:hover, #blog-tags li a:active,
#blog-tags li a:hover {
  text-decoration: underline; }

.tag-index {
  line-height: 1em;
  list-style-type: none; }
  .tag-index .tag-color-label {
    display: inline-block;
    /* [上][右][下][左] */
    margin: 0px 0px 10px 3px;
    padding: 3px 5px 3px 5px;
    /*border: solid #ccc 1px;*/
    /*background-color: #e7e7e7;*/ }
    .tag-index .tag-color-label div {
      padding: 1px 3px 3px 3px; }

.markdown-body {
  box-sizing: border-box;
  min-width: 200px;
  width: 100%;
  margin: 0 auto;
  /* [上][右][下][左] */
  padding: 0px 45px 45px 0px; }

#blog .blog__title {
  font-size: 28px;
  font-weight: bold;
  text-align: left; }
  #blog .blog__title span {
    display: block; }
  #blog .blog__title strong {
    color: #9f2121; }
  #blog .blog__title .aside {
    font-size: 32px; }

.post-link {
  margin-bottom: 0.75em;
  text-align: left; }
  .post-link .post-link-tags {
    text-align: left;
    margin-top: 10px; }

.tag-uline {
  line-height: 1em; }
  .tag-uline .tag-uline-label {
    display: inline-block;
    /* [上][右][下][左] */
    padding: 3px 5px 3px 5px;
    margin: 0px 3px 3px 0px;
    font-size: 12px;
    border-left-width: 5px;
    border-left-style: solid;
    box-shadow: 1px 1px 1px 1px #ccc; }

.tag-index {
  line-height: 1em;
  list-style-type: none; }
  .tag-index .tag-color-label {
    display: inline-block;
    /* [上][右][下][左] */
    margin: 0px 0px 10px 10px;
    padding: 5px 8px 5px 8px;
    /*background-color: #e7e7e7;*/
    border-left-width: 8px;
    border-left-style: solid;
    box-shadow: 1px 1px 1px 1px #ccc; }

.post-title {
  font-weight: 600;
  font-size: 1.25em;
  display: block;
  text-align: left; }

.post-date {
  color: #8a8a8a;
  font-family: Monaco, "Courier New", monospace;
  font-weight: 600;
  font-size: 0.65em;
  display: block;
  text-align: left; }

.post-description {
  display: block;
  text-align: left; }
